@import '../../styles/variables.scss';
@import '../../styles/mixin.scss';
.chat__footer{
    padding: 8px;
    width: 90%;
    margin: 0 auto;
    @include flexible(600px){
        width: 98%;
    }
}
.chat__footer-form{
    width: 92%;
    position: relative;
}
.chat__footer-form-btn{
    position: absolute;
    z-index: 10;
    right: 10px;
    top: 0px;
    bottom: 0px;
    margin: auto 0px;
    width: 30px;
    height: 30px;
    background-color: transparent;
    fill: $color-text;
    border: none;
    transition: .2s;
    transform: rotateY(180deg);
    &:hover{
        fill: $color-orange-hover;
    }
    &.hidden{
        opacity: 0;
        visibility: hidden;
    }
    &.show{
        opacity: 1;
        visibility:visible;
    }
}
.chat__input{
    padding: 12px 50px 12px 10px;
    border-radius: 15px;
    background-color: $bg-color-cream;
    outline: none;
    border: none;
    width: 100%;
    font-size: 18px;
    border: $border;
}
.chat__footer-actions{
    display: flex;
    gap: 15px;
    justify-content: space-between;
    align-items: center;
}
.chat__footer-emoji{
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    fill: $color-text;
    text-align: center;
    & > svg{
        width: 36px;
        height: 36px;
    }
}