@import '../../styles/variables.scss';
@import '../../styles/mixin.scss';
.chat-header {
    width: 100%;
    padding: 8px 25px 5px 25px;
    background-color: $bg-elements;
    border-bottom: $border;
    @include tablet(){
        padding: 8px 5px 5px 5px;
    }
}

.chat-header__text {
    font-size: 30px;
    color: $color-text;
    font-weight: 600;
    font-family: Segoe UI Historic, Segoe UI, Helvetica, Arial, sans-serif;
}
.chat-header__wrap{
    display: flex;
    align-items: center;
    gap: 15px;
    @include tablet(){
        margin: 0 auto;
        justify-content: space-around;
    }
    @include phone(){
        width: 100%;
    }
}
.chat-header__img{
    width: 60px;
    height: 60px;
    border-radius: 50%;

}
.chat-header__arrow{
    margin-right: 15px;
    fill: $color-text;
    transition: .2s;
    width: 45px;
    cursor: pointer;
    height: 45px;
    & > svg{
        width: inherit;
        height: inherit;
    }
    &:hover{
        fill: $color-orange-hover;
    }
    @include tablet(){
        margin: 0px;
    }
}